<template>
    <div id="group-item" class="group-item-panel">
        <div class="group-item-wrapper">
            <label class="group-item-label">
                <input type="checkbox" class="group-item-checkbox"
                v-model="checked"
                @change="handleSelectedCheckbox">{{ getItemName }}
            </label>
        </div>
    </div>
</template>
<script>
export default {
    name: 'GroupItem',
    props: {
        item: {
            type: String
        },
        type: {
            type: String
        },
        filterList: {
            type: Array,
            default: []
        }
    },
    data() {
        return {
            checked: false
        }
    },
    computed: {
        getItemName() {
            if (this.item < 0) {
                return 'All Group'
            } else {
                return `Group - ${ this.item }`
            }
        }
    },
    methods: {
        handleSelectedCheckbox() {
            this.$emit('select-checkbox', this.item, this.checked);
        },
        setCheckBox() {
            if (this.type === window.CONSTANTS.TOP_BUTTON_TYPE.FILTER) {
                this.checked = !this._.includes(this.filterList, this.item);
            } else {
                this.checked = false;
            }
        }
    },
    created() {
        this.setCheckBox();
    },
    watch: {
        type: function() {
            this.setCheckBox();
        }
    }
}
</script>
<style>
.group-item-panel {
    position: relative;
    width: 100%;
    height: 45px;
    padding: 15px;
    border-bottom: 1px solid rgb(235, 235, 235);
}
.group-item-wrapper {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}
.group-item-label {
    font-size: 20px;
    user-select: none;
    color: rgb(128, 128, 128);
    font-weight: bold;
    cursor: pointer;
}
.group-item-checkbox {
    margin: -1px 10px 0 !important;
    width: 17px;
    height: 17px;
    vertical-align: middle;
}
</style>
