<template>
    <div id="contextmenu" class="menu-container" ref="container" :style="styleOption">
        <MenuItem v-if="isSelected" :speaker="speaker"
        @select-menu="handleSelectMenu"></MenuItem>
    </div>
</template>
<script>
import MenuItem from '@/components/MenuItem';
export default {
    name: 'ContextMenu',
    components: {
        MenuItem
    },
    props: {
        speaker: {
            type: Object,
            required: true
        },
        position: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            selectedType: '',
            top: 0,
            left: 0
        }
    },
    methods: {
        handleSelectMenu(type) {
            // this.selectedType = type;
            const lowerType = type.toLowerCase();
        },
        setContextMenuPosition() {
            const container = this.$refs.container;
            if (!!container) {
                const width = container.clientWidth,
                      height = container.clientHeight;
                this.top = Math.round(this.position.y - height + 40);
                this.left = Math.round(this.position.x - (width / 2));
            }
        }
    },
    computed: {
        isSelected() {
            return !!!this.selectedType;
        },
        styleOption() {
            return {
                top: `${ this.top }px`,
                left: `${ this.left }px`
            }
        }
    },
    mounted() {
        this.setContextMenuPosition();
    },
    watch: {
        position: function() {
            this.setContextMenuPosition();
        }
    }
}
</script>
<style>
.menu-container {
    position: absolute;
    min-width: 180px;
    max-width: 230px;
    height: auto;
    z-index: 2;
    border-radius: 15px;
    color: white;
    background-color: rgb(250, 115, 120);
    overflow: hidden;
    box-shadow: 10px 10px 25px rgba(40, 40, 40, 0.3);
}
</style>