<template>
    <div id="group-list" class="group-container" :class="type">
        <div class="group-frame">
            <!-- <GroupItem :item="allGroupKey" @select-checkbox="handleChangedCheckBox"></GroupItem> -->
            <GroupItem v-for="(item, index) in groupList"
            :key="index" :item="item" :filterList="checkList" :type="type" @select-checkbox="handleChangedCheckBox"></GroupItem>
        </div>
        <div v-if="isShowingButton" class="group-button-frame"
        @click="handleSelectButton">
            <div class="group-button-panel" :class="{ deactive: isEmpty }">
                <div class="group-button-text">{{ buttonText }}</div>
            </div>
        </div>
    </div>
</template>
<script>
import GroupItem from '@/components/topList/GroupItem';
export default {
    name: 'GroupList',
    components: {
        GroupItem
    },
    props: {
        groupList: {
            type: Array,
            default: []
        },
        checkList: {
            type: Array,
            default: []
        },
        type: {
            type: String
        }
    },
    data() {
        return {
            allGroupKey: '-1',
            broadcastList: []
        }
    },
    methods: {
        handleChangedCheckBox(item, checked) {
            if (this.type === window.CONSTANTS.TOP_BUTTON_TYPE.FILTER) {
                this.$emit('select-checkbox', item, checked);
            } else {
                if (!this._.includes(this.broadcastList, item)) {
                    this.broadcastList.push(item);
                } else {
                    this.broadcastList = this._.without(this.broadcastList, item);
                }
            }
        },
        handleSelectButton(e) {
            e.stopPropagation();
            this.$emit('select-button', this.broadcastList);
        }
    },
    computed: {
        isShowingButton() {
            return this.type === window.CONSTANTS.TOP_BUTTON_TYPE.BROADCAST;
        },
        isEmpty() {
            return this._.isEmpty(this.broadcastList);
        },
        buttonText() {
            return this.isEmpty? 'SELECT YOUR GROUP': 'START BROADCAST';
        }
    },
    created() {
        console.log("Create filter list ", this.groupList);
    }
}
</script>
